@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200;400;800;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

::-webkit-scrollbar{
  width: 5px;

}

::-webkit-scrollbar-track{
  background-color: #030518 ;
}


::-webkit-scrollbar-thumb{
  background-color: #25D6DA ;
  border-radius: 20px;
}


body {
  background: #111111;
  font-family: 'Montserrat',sans-serif;    
}

.App {
  text-align: center;
}



.container-1 {
  display: flex;
  height: 95vh;
  width: 100%;
  margin: 0 auto;
  justify-content: center;
  align-items: center;
}
.container-1 h1{
  color: #d1f7ff
}


.shape {
  z-index: 0;
  -webkit-animation: morph 8s ease-in-out infinite;
          animation: morph 8s ease-in-out infinite;
  border-radius:  60% 40% 30% 70%/60% 30% 70% 40%;
  border: 0.5rem solid #05d9e8;
  height: 30vh;
  transition: ease-in-out 0.5s ;
  width: 15vw;
  
}
.main{
  padding-left: 5vw;
  justify-content: center;
  align-items: center;
    
}

.main h1{
  color: #f1f1f1;
  text-transform: uppercase;
  font-weight: 800;
  font-size: 2rem;
}

.main p{
  color: #818181;
  font-weight: 400;
  font-size: 0.9rem;
  line-height: 2rem;
  
}

.box{
  display: inline-block;
}

.icones{
  padding-right: 1rem;
  font-size: 1.5rem;
  color: #f1f1f1;
}

.icones:hover{
  transition: 0.5s;
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}



.container-2{
  padding-top: 3rem;
  padding-bottom: 3rem;
  margin: 0 auto;
  height: 100%;
  width: 60%;
  text-align: justify;
  justify-content: center;
  display: block;
}


.outdoor{
  height: 50vh;
  width: 30vw;
  padding-left:50% ;
  background: url(/static/media/deskM.adb51e6f.JPG);
  background-position: center;
  background-size: cover ;

}

.content{
  display: flex;
}
.about{
  margin: 0 auto;
  padding-top: 1rem;
}
.about h1{
  color: #f1f1f1;
  font-weight: 500;
  font-size: 4vh;
  
}
.about p{
  width: 25vw;
  color: #818181;
  font-size: 1em;
  font-weight: 400;
  line-height: 2rem;
  
  
}

.about li{
  list-style: none;
  color: #f1f1f1;
  padding-top: 0.5rem;
  font-size: 1em;
  line-height: 2rem;
}

.infos{
  margin-left: 5rem;
  margin-top: 15%;
  width: 70%;
}
.infos span{
  
  color: #818181;
}

.habilidades{
  margin: 0 auto ;
  text-align: center;
}
.habilidades h1{ 
    text-align: left;
    color: #f1f1f1;
    font-weight: 500;
    font-size: 4vh;
}

.habilidades p{
  color: #818181;
  font-size: 1em;
  font-weight: 400;
  line-height: 2rem;
  
}
.box p{
  font-family: 'Montserrat',sans-serif;  
  font-size: 1rem;
  font-weight: 500 ;
  color: #F231A5;
}
.habilidades .box{
  padding-top: 3rem;
}
.habilidades .icones{
  -webkit-margin-start: 1rem;
          margin-inline-start: 1rem;
  font-size: 4rem;
}


.container-3{
  margin: 0 auto;
  margin-top: 3rem;
  display: block;
  width: 60%;
  color: white;
}

.container-3 h1{
  text-align: left;
  color: #f1f1f1;
  font-weight: 500;
  font-size: 4vh;
}

.grid-template-columns {
  display: grid;
  padding-top:  5%;
  padding-bottom: 5%;
  grid-gap: 2rem;
  gap: 2rem;
  grid-template-columns: 48% 48%;
}

.grid-template-columns a{
  text-decoration: none;
}

.item {
  margin-top: 2%;
  margin-left: 2%;
  height: 30vh;
  width: 95%;
  background-size: cover;
  border: 2px solid #e7e7e7;
  border-radius: 5px;
}
.item:hover{
  border: 2px solid #F231A5;
  transition: ease-in-out, 0.5s;
}


.cortai{
  background-image: url(/static/media/Cortai-project.6d1d48de.png);
}

.cmemoria{
  background-image: url(/static/media/Centro-memoria-project.db9f67f2.png);
}

.mais-ifmg{
  background-image: url(/static/media/mais-ifmg-project.2cf39283.png);
}

.autohouse{
  background-image: url(/static/media/autohouse-project.05afc8d4.png);
}


.close {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
  -webkit-user-select: none ;
      -ms-user-select: none ;
          user-select: none ;
}


.close:hover,
.close:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}

.padding{
  margin-top: 2%;
  margin-left: 2.5%;
  width: 95%;
}

.project-container{
  text-align: justify;
  line-height: 2rem;  
  border-radius: 10px;
  width: 95%;
}

.project-container:hover{
  background: #212121;
  transition: ease-in-out, 0.5s;
  border: 1px solid #000000;

}

.project-container h1{
  padding-top: 0.5rem;
  text-align: justify;
  color: #25D6DA;
  font-size: 1.5rem;
  font-weight: bold;
}


.project-container p{
  font-size: 0.8rem;
}

.tags{
  display: flex;
}

.tag{
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 1rem;
  border: 1px solid white;
  border-radius: 20px;
  font-size: 0.8rem;
  
}
.tag:hover{
  background-color: #F231A5;
  transition: ease-in-out 0.3s;
  cursor: pointer;
}

.footer{
  padding-top: 5rem;
  position: relative;
  display: flex;
  justify-content: space-evenly;
  align-items: center ;
  height: 100%;
  width: 100%;
  margin: 0 auto;
  padding-bottom: 4rem;
  justify-content: center;
  align-items: center;
}

.footer-text{
  text-align: justify;
  align-self: stretch;
  width: 30%;
  color: white;
}


.footer-form{
  padding-top: 2rem;
  padding-bottom: 2rem;
  padding-left: 1.2rem;
  text-align: justify;
  min-height: 300px;
  width: 30%;
  background-color: #030518;
  border-radius: 15px;
  color: white;
}


#contact-form input{
  background-color: #111111;
  margin-top: 1em;
  margin-bottom: 1em;
  padding-left: 1em;
  height: 2rem;
  width: 90%;
  color: white;
  border: 1px solid white;
  border-radius: 7px;
}

#contact-form textarea{
  
  background-color: #111111;
  resize: none;
  width: 90%;
  height: 10em;
  padding-left: 1em;
  padding-top: 1em;
  margin-top: 1em;
  color: white;
  border: 1px solid white;
  border-radius: 7px;
}  

#contact-form .button{
  
  border:none;
  background-color: #F231A5;
  width: 95%;
  margin-bottom: -1em;
}

#contact-form .button:hover{
  background-color: #F09;
  -webkit-transform: scale(1.02);
          transform: scale(1.02);
  transition: ease-in-out 0.3s;
}

.sub-footer{
  background-color: #000000;
  color: white;
  height: 2rem;
  padding-top: 1rem;
  text-align:center; 
  font-size: 0.5;
  
}


@-webkit-keyframes morph {
  0% {
    border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
    background-image: url(/static/media/me.ea098699.jpg);
    background-position: center;
    background-size: cover;
    
  }
  50% {
    border-radius: 30% 60% 70% 40%/50% 60% 30% 60%;
    background-image: url(/static/media/me.ea098699.jpg);
    background-position: center;
    background-size: cover;
  }
  100% {
    border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
    background-image: url(/static/media/me.ea098699.jpg);
    background-position: center;
    background-size: cover;
  } 
}


@keyframes morph {
  0% {
    border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
    background-image: url(/static/media/me.ea098699.jpg);
    background-position: center;
    background-size: cover;
    
  }
  50% {
    border-radius: 30% 60% 70% 40%/50% 60% 30% 60%;
    background-image: url(/static/media/me.ea098699.jpg);
    background-position: center;
    background-size: cover;
  }
  100% {
    border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
    background-image: url(/static/media/me.ea098699.jpg);
    background-position: center;
    background-size: cover;
  } 
}

